<template>
<main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> 23621</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;    &nbsp; . 
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary" v-if="orderInfo">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{orderInfo.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                  
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in orderInfoItems">
              <h3>{{ item.product.title }}</h3>
              <b>{{ (item.quantity * item.product.price) | numberFormat }} &#8381;</b>
              <span>: {{ item.product.id || numberFormat }}</span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>: <b>500 &#8381;</b></p>
            <p>: <b>{{ totalPositionInfo }}</b>    <b>{{ (totalPriceInfo + 500) | numberFormat }} &#8381;</b></p>
          </div>
        </div>
      </form>
    </section>
  </main> 
</template>

<script>
import { mapGetters, mapState } from "vuex";
import numberFormat from "@/helpers/numberFormat";

export default {
	filters: { numberFormat },
	computed: {
      ...mapGetters({orderInfoItems: 'orderInfoItems', totalPriceInfo: 'totalPriceInfo', totalPositionInfo: 'totalPositionInfo'}),
      ...mapState({orderInfo: 'orderInfo'})      
    },
	created() {
		if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {      
			return;
		}
		this.$store.dispatch('loadOrderInfo', this.$route.params.id)
	}
} 
</script>
