<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item"> 
          <router-link class="breadcrumbs__link" href="index.html" :to="{name:'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            
          </a> 
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <div v-if="cartLoading"> ...</div>
      <div v-if="cartLoadingFailed && totalPosition < 1">    </div>
      <span class="content__info" v-if="!cartLoading && !cartLoadingFailed">
        {{ totalPosition }} 
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products"
                      :key="item.productId"
                      :item="item" />            
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            &nbsp;   &nbsp; 
          </p>
          <p class="cart__price">
            : <span>{{ totalPrice | numberFormat }} &#8381;</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" :disabled="!totalPosition" type="submit">
             
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import { mapGetters } from "vuex";
  import numberFormat from "@/helpers/numberFormat";  
  import CartItem from "@/components/CartItem";

  export default {
    components: { CartItem },
  	filters: { numberFormat },
  	computed: {
  		...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalPosition: 'cartTotalPosition',
                    cartLoading: 'cartLoading', cartLoadingFailed: 'cartLoadingFailed'})  		
  	}
  }
</script>