<template>
  <main class="content container" v-if="productLoading"> ...</main>
  <main class="content container" v-else-if="!productData">   </main>
  <main class="content container" v-else="productData">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">
            
          </router-link> 
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">
            {{ category.title }}
          </router-link>
        </li> 
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image" alt="product.title">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ product.price | numberFormat }} &#8381; 
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">:</legend>
              <ul class="colors">
                <li class="colors__item" v-for="color in product.colors" :key='color.code'>
                  <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio" name="color-item" :value="color.code">
                    <span class="colors__value" :style="{'background-color': color.code}">                    
                    </span>
                  </label>
                </li>                
              </ul>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">  :</legend>

              <ul class="sizes sizes--primery">
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="32" >
                    <span class="sizes__value">
                      32gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">
                    <span class="sizes__value">
                      64gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="128" checked="">
                    <span class="sizes__value">
                      128gb
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">
                <button type="button" aria-label="  " @click="decrementAmount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount">

                <button type="button" aria-label="  " @click="incrementAmount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button class="button button--primery" type="submit" :disabled="productAddSending">
                 
              </button>
            </div>
            <div v-show="productAdded">   </div>
            <div v-show="productAddSending">   ...</div> 
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
                
            </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
             GPS, , BEIDOU Galileo  QZSS<br>
              <br>
              Bluetooth Smart, ANT+  Wi-Fi<br>
              <br>
          </p>
  
          <a href="#">
             
          </a>
  
          <h3> ?</h3>
  
          <p>
            Wahoo ELEMNT BOLT GPS   ,     ,    . Wahoo ELEMNT BOLT GPS    ANT+,     .    ,     .          ,      .    ,      .          .
          </p>
  
          <h3></h3>
  
          <p>
             Wahoo ELEMNT BOLT  .     74,6 x 47,3 x 22,1 .     .      .        56 .      ,        : ,   ,   .. (     ).        IPX7. ,     ,     ( 30 )        1.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>
<script>
  import gotoPage from '@/helpers/gotoPage';
  import numberFormat from '@/helpers/numberFormat';
  import axios from 'axios';
  import { API_BASE_URL } from '../config';
  import { mapActions } from 'vuex';

  export default {
    data() {
      return {
        productAmount: 1,
        productData: null,

        productLoading: false,
        productLoadingFailed: false,

        productAdded: false,
        productAddSending: false
      }
    },
  	filters: {
  		numberFormat
  	},
  	computed: {
  		product() {
  			return this.productData 
        ?{...this.productData, image: this.productData.image.file.url}
        :{}
  		},
  		category() {
  			return this.productData.category;
      }
  	},
  	methods: {
        ...mapActions(['addProductToCart']),
        gotoPage,
        addToCart() {
          this.productAdded = false;
          this.productAddSending = true;

          this.addProductToCart({
              productId: this.product.id,
              amount: this.productAmount
            })
            .then(() => {
              this.productAdded = true;
              this.productAddSending = false;
            })
        },
        incrementAmount() {
          return ++this.productAmount
        },
        decrementAmount() {
          if(this.productAmount > 1) {
            return --this.productAmount
          }
        },
        loadProduct() {
          this.productLoading = true;
          this.productLoadingFailed = false;

          axios.get(API_BASE_URL + '/api/products/' + this.$route.params.id)
               .then(response => this.productData = response.data)
               .catch(() => this.productLoadingFailed = true)
               .then(() => this.productLoading = false)
        }
    },    
    watch: {
      '$route.params.id': {
        handler() {
          this.loadProduct();
        },
        immediate: true
      }
    }
  }

</script>